
import { Client } from "@/composables/client";
import {
  IonPage,
  IonLabel,
  IonItem,
  IonTitle,
  IonToolbar,
  IonHeader,
  IonButton,
  IonFooter,
  IonContent,
} from "@ionic/vue";
import { defineComponent } from "vue";

import moment from "moment";
import router from "@/router";

export default defineComponent({
  components: {
    IonPage,
    IonLabel,
    IonItem,
    IonTitle,
    IonToolbar,
    IonHeader,
    IonButton,
    IonFooter,
    IonContent,
  },
  created() {
    this.init();
  },
  data() {
    return {
      details: {} as any,
      moment: moment,
    };
  },
  methods: {
    goBack(): void {
      router.back();
    },
    async init(): Promise<void> {
      const client = Client();

      const res = await client.getJson(`clients/${this.$route.params.id}`);

      this.details = res?.data;
    },
    viewOrders(id: number): void {
      this.$router.push(`/tests?patient_id=${id}`);
    },
    newOrder(id: number): void {
      this.$router.push(`/order/${id}`);
    },
  },
});
